.rank {
  padding: 1rem 1.25rem;
}
.rankList {
  border-radius: .4375rem;
  list-style: none;
  overflow: hidden;
  width: 100%;
  -webkit-box-shadow: 0 0.75rem 1.875rem rgba(0, 0, 0, 0.05);
  box-shadow: 0 0.75rem 1.875rem rgba(0, 0, 0, 0.05);
  margin-bottom: 0.5rem;
}
.rankList > div {
  float: left;
}
.rankList > div:nth-of-type(1) {
  width: 30%;
  height: 100%;
}
.rankList > div:nth-of-type(1) img {
  width: 100%;
}
.rankList > div:nth-of-type(2) {
  padding: 0 1rem;
  padding-top: 0.6em;
  width: 60%;
}
.rankTxt p {
  line-height: 1.3rem;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}
.topTitle {
  font-size: 0.9rem;
  line-height: 1.5rem;
}



